import React from 'react'
import { graphql } from 'gatsby'
import { Link } from 'gatsby'
import Img from 'gatsby-image'
import { MDXRenderer } from "gatsby-plugin-mdx"
import { MDXProvider } from "@mdx-js/react"
import DioramaLayout from "../components/diorama-layout"
import { MDXSharpImg, MDXSrcImg, SafeFluid } from '../components/images';
import { Safe } from '../components/utils';
import Typed from 'react-typed'

const shortcodes = {
   Img,
   Link,
   Typed,
}

export default ({ data }) => {
   const body = data.mdx.body
   const frontmatter = data.mdx.frontmatter
   const images = data.mdx.fields.images

   const imgs = {};
   if (images) {
      images.forEach((image, i) => {
         const { childImageSharp: c, publicURL } = Safe(image);
         const { fluid: f } = Safe(c);
         imgs[`Image${i + 1}`] = ({ align, width }) =>
         f ? (
            <MDXSharpImg align={align} width={width} fluid={SafeFluid(f)} />
         ) : (
            <MDXSrcImg align={align} width={width} src={publicURL || ''} />
         );
      });
   }

   return (
//const IndexPage = ({ data }) => (
   <DioramaLayout>
      <article className="diorama">
         <div className="diorama__inner">
            <div className="diorama__body">
               <div className="diorama__banner">
                  {/* <Img fluid={frontmatter.bodyImage.childImageSharp.fluid} /> */}
               </div>
               <h1 className="diorama__title">{frontmatter.title}</h1>
               <div className="diorama__lead">{frontmatter.summary}</div>
               <MDXProvider components={shortcodes}>
                  <MDXRenderer imgs={imgs} frontmatter={frontmatter}>{body}</MDXRenderer>
               </MDXProvider>
            </div>
         </div>
      </article>
   </DioramaLayout>
   )
}

export const query = graphql`
   query($slug: String!) {
      mdx(fields: { slug: { eq: $slug } }) {
         frontmatter {
            title
         }
         fields {
            imageCount
            images {
               childImageSharp {
                  fluid(maxWidth: 600) {
                     ...GatsbyImageSharpFluid
                  }
               }
            }
         }
         body
      }
   }
`